import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label,
  FormFeedback
  
  //CustomInput
} from 'reactstrap'
import React, { Fragment, useState } from 'react'
import { getFootballSquadadd  } from '../../store/actions'
import { useDispatch } from 'react-redux'
import Breadcrumbs from '@components/breadcrumbs'
import Select from 'react-select'
import * as yup from 'yup'
import { useForm, Controller} from 'react-hook-form'
import classnames from 'classnames'
import { yupResolver } from '@hookform/resolvers/yup'
import { selectThemeColors } from '@utils'
import '@styles/react/libs/flatpickr/flatpickr.scss'

const selectcategory = [
  { value: '1', label: 'ST' },
  { value: '2', label: 'MID' },
  { value: '3', label: 'DEF' },
  { value: '4', label: 'GK' }
 
]
const VerticalForm = () => {
  const dispatch = useDispatch()
  const [squadform, setSquadform] = useState({ player_api:"", player_api_id:"", player_name:"", team_id:"", player_type:"", player_value:"", active:"" })
  const SignupSchema = yup.object().shape({
   
    playerapi: yup.string().required(),
    player_Api_Id: yup.string().required(),
    player_name:yup.string().required(),
    team_id:yup.string().required(),
    active:yup.string().required(),
    player_value:yup.string().required()
    
  })
const [err, setErr] = useState(false)
const defaultValues = {

  reactSelect: null
  
  }
  console.log(err)
  
  const { register, errors, handleSubmit, control } = useForm(defaultValues, { mode: 'onChange', resolver: yupResolver(SignupSchema) })
// const handleChange = (e) => {
//   const [name, value] = e.target
//   setSquadform(prv => {
//     return {...prv, [name]:value}
//   })
// }
  
  const onSubmit = () => {
   
    dispatch(getFootballSquadadd(squadform))
    setErr(true)
  }
  const onclear = (e) => {
    e.preventDefault()
    setSquadform({ player_api:"", player_api_id:"", player_name:"", team_id:"", player_type:"", player_value:"", active:"" })
  }
  const editcategory = (e) => {
    setSquadform({...squadform, player_type:e.value})
  }
  return (
    <Fragment>
       <Breadcrumbs breadCrumbTitle='Football Squad' breadCrumbParent='footballsquad' breadCrumbActive='Add' />
    <Card>
      <CardHeader>
        <CardTitle tag="h4">Add Cricket squad</CardTitle>
      </CardHeader>
      <CardBody>
        <Form onSubmit={handleSubmit(onSubmit)} >
          <Row>
          <Col sm="12">
              <FormGroup>
                <Label for='playerApi'>Player Api</Label>
                <Input
                  type="text"
                  name="playerapi"
                  id="nameVertical"
                  value={squadform.player_api}
                  onChange={(e) => setSquadform({ ...squadform, player_api: e.target.value })}
                  innerRef={register({ required: true })}
                  invalid={errors.playerapi && true}
                  placeholder="Enter Product Name"
                />
                {errors && errors.playerapi && <FormFeedback>{errors.playerapi.message}</FormFeedback>}
              </FormGroup>
            </Col>
          <Col sm="12">
              <FormGroup>
                <Label for="playerapiid">Player Api Id</Label>
                <Input
                  type="number"
                  name="player_Api_Id"
                  id="playerapiid"
                  value={squadform.player_api_id}
                  onChange={(e) => setSquadform({ ...squadform, player_api_id: e.target.value })}
                  innerRef={register({ required: true })}
                  invalid={errors.player_Api_Id && true}
                  placeholder="Enter No of ID "
                />
                {errors && errors.player_Api_Id && <FormFeedback>{errors.player_Api_Id.message}</FormFeedback>}
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for="playerName">Player Name</Label>
                <Input
                  type="text"
                  name="player_name"
                  id="EmailVertical"
                  value={squadform.player_name}
                  innerRef={register({ required: true })}
                  invalid={errors.player_name && true}
                  onChange={(e) => setSquadform({ ...squadform, player_name: e.target.value })}
                  placeholder="Enter No of ID "
                />
                {errors && errors.player_name && <FormFeedback>{errors.player_name.message}</FormFeedback>}
              </FormGroup>
            </Col>  
            <Col sm="12">
              <FormGroup>
                <Label for='id'>Team Id</Label>
                <Input
                  type="text"
                  name="team_id"
                  id="nameVertical"
                  value={squadform.team_id}
                  onChange={(e) => setSquadform({ ...squadform, team_id: e.target.value })}
                  innerRef={register({ required: true })}
                  invalid={errors.team_id && true}
                  placeholder="Enter Product Name"
                />
                {errors && errors.team_id && <FormFeedback>{errors.team_id.message}</FormFeedback>}
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for="nameVertical">player Type</Label>
                <Controller
                   control={control}
                   render={({ field: { ...field } }) => (

                  <Select
                     {...field}
                   onChange={(e) => editcategory(e)}
                   options={selectcategory}
                   theme={selectThemeColors}
                  className={classnames('react-select', { 'is-invalid': squadform.player_type === '' ? err : setErr(true)  })}
                   classNamePrefix='select'
                  />
               )}

                     name='reactSelect'
                   />
                 {errors && errors.player_type && <FormFeedback>{errors.player_type.message}</FormFeedback>}
              </FormGroup>
            </Col>
            
            <Col  sm="12">
              <FormGroup>
                <Label for="playerValue">player Value</Label>
                <Input
                  type="number"
                  name="player_value"
                  id="nameVertical"
                  value={squadform.player_value}
                  onChange={(e) => setSquadform({ ...squadform, player_value: e.target.value })}
                  innerRef={register({ required: true })}
                  invalid={errors.player_value && true}
                  placeholder="Enter Product Name"
                />
                 {errors && errors.player_value && <FormFeedback>{errors.player_value.message}</FormFeedback>}
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for="active">Active</Label>
                <Input
                  type="number"
                  name="active"
                  id="nameVertical"
                  value={squadform.active}
                  onChange={(e) => setSquadform({ ...squadform, active: e.target.value })}
                  innerRef={register({ required: true })}
                  invalid={errors.active && true}
                  placeholder="Enter Product Name"
                />
                 {errors && errors.active && <FormFeedback>{errors.active.message}</FormFeedback>}
              </FormGroup>
            </Col>
           <Col sm="12">
              <FormGroup className="d-flex mb-0">
                <Button.Ripple
                  className="mr-1"
                  color="primary"
                  type="submit"
                  
                >
                  Submit
                </Button.Ripple>
                <Button.Ripple
                  outline
                  color="secondary"
                  type="reset"
                  onClick={(e) => onclear(e)}
                >
                  Reset
                </Button.Ripple>
              </FormGroup>
            </Col>
          </Row>
        </Form>
      </CardBody>
    </Card>
    </Fragment>
  )
}
export default VerticalForm
